import pokerDiceConsts from './pokerDiceConsts.js';
import pokerDiceUtils from './pokerDiceUtils.js';
import rerollUtils from './rerollUtils.js';

export default {
    /**
     * 주사위 면 배열을 입력받아 이에 해당하는 포커 조합을 반환한다.
     * @param {number[]} dice 주사위 면 배열 (두 자리 정수의 배열)
     * @return {number} 입력받은 주사위 면 배열에 해당하는 포커 조합 (여섯 자리 정수 또는 0: INVALID)
     */
    pokerHand: (dice) => {
        if (dice.length!=pokerDiceConsts.NUM_DICE) {
            return 0;
        } else {
            return pokerDiceUtils.pokerHand(dice[0], dice[1], dice[2], dice[3], dice[4]);
        }
    },
    /**
     * 주사위를 다시 굴릴 때 다시 굴릴 주사위를 판별한다.
     * @param {number[]} dice 
     * @param {number} playerHand 
     * @returns {number} 다시 굴릴 주사위를 0 이상 31 이하의 정수로 반환한다. (1: 다시 굴릴 주사위, 0: 고정 시킬 주사위)
     */
    optReroll: (dice, playerHand) => {
        if (dice.length!=pokerDiceConsts.NUM_DICE) {
            return 0;
        } else {
            return rerollUtils.optReroll(dice[0], dice[1], dice[2], dice[3], dice[4], playerHand);
        }
    },
    intToBinArray: (intVal) => {
        let result = [];
        let probe = 1;
        while (result.length<5) {
            result.push(intVal & probe ? 1 : 0);
            probe <<= 1;
        }
        return result;
    }
};