<template>
  <header>
    <div class="container">
      <div class="center">
        The Eighth Project: Poker Dice<br/>
        with Vue.js
      </div>
      <div class="right20">
        sianke1991
      </div>
    </div>
  </header>
  <section @mouseover="isMouseOver">
    <div class="container">

      <!-- oppontSide -->
      <div class="row emptyRow10"></div>
      <div class="row oppontSide">
        <div class="col-md-2 col-sm-2">점수: {{oppontScore}}</div>
        <div class="col-md-3 col-sm-3">조합: {{pokerDiceUtils.handToString(oppontHand)}}</div>
        <div class="col-md-7 col-sm-7">
          <img :src="pokerDiceUtils.dieToImgFileName(oppontDice[0])" class="blackBorder" />
          <img :src="pokerDiceUtils.dieToImgFileName(oppontDice[1])" class="blackBorder" />
          <img :src="pokerDiceUtils.dieToImgFileName(oppontDice[2])" class="blackBorder" />
          <img :src="pokerDiceUtils.dieToImgFileName(oppontDice[3])" class="blackBorder" />
          <img :src="pokerDiceUtils.dieToImgFileName(oppontDice[4])" class="blackBorder" />
        </div>
      </div>

      <!-- roundIndicator -->
      <div class="row emptyRow10"></div>
      <div class="row roundIndicator">
        <div class="col-md-3 col-sm-3"><span class="test hidden">{{pokerDiceUtils.pokerHand(diceInput0, diceInput1, diceInput2, diceInput3, diceInput4)}}</span></div>
        <div class="col-md-3 col-sm-3 center">{{currentRound}} 번째 라운드 ({{currentDealer==pokerDiceConsts.PLAYER ? "플레이어":"컴퓨터"}} 의 선)</div>
        <div class="col-md-3 col-sm-3 center">{{phaseStr}}</div>
        <div class="col-md-3 col-sm-3"><span class="test hidden">{{pokerDiceUtils.handToString(pokerDiceUtils.pokerHand(diceInput0, diceInput1, diceInput2, diceInput3, diceInput4))}}, {{arrayUtils.intToBinArray(rerollUtils.optReroll(diceInput0, diceInput1, diceInput2, diceInput3, diceInput4, 0))}}</span></div>
      </div>

      <!-- playerSide -->
      <div class="row emptyRow10"></div>
      <div class="row playerSide">
        <div class="col-md-2 col-sm-2">점수: {{playerScore}}</div>
        <div class="col-md-3 col-sm-3">조합: {{pokerDiceUtils.handToString(playerHand)}}</div>
        <div class="col-md-7 col-sm-7">
          <img :src="pokerDiceUtils.dieToImgFileName(playerDice[0])" class="blackBorder" @click="clickPlayerDice(0)"/>
          <img :src="pokerDiceUtils.dieToImgFileName(playerDice[1])" class="blackBorder" @click="clickPlayerDice(1)"/>
          <img :src="pokerDiceUtils.dieToImgFileName(playerDice[2])" class="blackBorder" @click="clickPlayerDice(2)"/>
          <img :src="pokerDiceUtils.dieToImgFileName(playerDice[3])" class="blackBorder" @click="clickPlayerDice(3)"/>
          <img :src="pokerDiceUtils.dieToImgFileName(playerDice[4])" class="blackBorder" @click="clickPlayerDice(4)"/>
        </div>
      </div>

      <!-- buttonSide -->
      <div class="row emptyRow10"></div>
      <div class="buttonSide center">
        <div class="btn-group mx-3" role="group" aria-label="Basic outlined example">
          <button class="btn btn-outline-primary" type="button" :disabled="!((currentState==1 || currentState==3)&&(playerReroll!=0))" @click="clickRerollButton">다시 굴리기</button>
          <button class="btn btn-outline-primary" type="button" :disabled="!((currentState==1 || currentState==3)&&(playerReroll==0))" @click="clickFixallButton">그만 굴리기</button>
          <button class="btn btn-outline-primary" type="button" :disabled="!(currentState==5 || currentState==15 || currentState==20)" @click="clickCheckButton">확인</button>
        </div>

        <button type="button" class="btn btn-info" data-bs-toggle="modal" data-bs-target="#exampleModal">도움말</button>
        <button type="button" class="btn btn-outline-secondary mx-3" :disabled="currentState!=23" @click="clickResetButton">처음으로</button>
      </div>

      <!-- messageSide -->
      <div class="row emptyRow10"></div>
      <div class="row messageSide">
        <div class="col-md-2 col-sm-2"></div>
        <div class="col-md-8 col-sm-8">{{messageStr}}</div>
      </div>
    </div>
  </section>

  <!-- testSection -->
  <!-- document.querySelector('.test').removeAttribute('class') -->
  <div class="test hidden">
    <div class="center">
      주사위 굴리기 시뮬레이션
    </div>
    <div class="row center">
      <div class="col-md-2 col-sm-2"></div>
      <input class="col-md-1 col-sm-1" v-model="diceInput0" />
      <input class="col-md-1 col-sm-1" v-model="diceInput1" />
      <input class="col-md-1 col-sm-1" v-model="diceInput2" />
      <input class="col-md-1 col-sm-1" v-model="diceInput3" />
      <input class="col-md-1 col-sm-1" v-model="diceInput4" />
      <button class="col-md-1 col-sm-1 btn btn-outline-dark mx-2" @click="rerollTestDice">reroll</button>
      <button class="col-md-1 col-sm-1 btn btn-outline-warning" @click="resetTestDice">reset</button>
    </div>
  </div>

  <!-- Modal -->
  <div class="modal fade" id="exampleModal" tabindex="-1" aria-labelledby="exampleModalLabel" aria-hidden="true">
    <div class="modal-dialog">
      <div class="modal-content">
        <div class="modal-header">
          <h5 class="modal-title" id="exampleModalLabel">도움말 페이지: {{instPage}}</h5>
          <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
        </div>
        <div class="modal-body" v-html="instructions[instPage-1]">
        </div>
        <div class="modal-footer" style="justify-content: center">
          <div class="btn-group" role="group" aria-label="Basic example">
            <button type="button" class="btn btn-outline-primary" :disabled="instPage<=1" @click="instPage--;">이전글</button>
            <button type="button" class="btn btn-outline-primary" data-bs-dismiss="modal">닫기</button>
            <button type="button" class="btn btn-outline-primary" :disabled="instPage>=instructions.length" @click="instPage++;">다음글</button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import pokerDiceConsts from './assets/pokerDiceConsts.js';
import pokerDiceUtils from './assets/pokerDiceUtils.js';
import rerollUtils from './assets/rerollUtils.js';
import arrayUtils from './assets/arrayUtils.js';
import instructions from './assets/instructions.js';
import messages from './assets/messages.js';
import phases from './assets/phases.js';
export default {
  name: 'App',
  data() {
    return {
      pokerDiceConsts,
      pokerDiceUtils,
      rerollUtils,
      arrayUtils,
      instructions,
      messages,
      phases,
      
      currentState: 0,
      currentRound: 1,
      currentDealer: 0,
      
      oppontScore: 0,
      oppontHand: 0,
      oppontDice: [0, 0, 0, 0, 0],
      oppontReroll: 0,

      playerScore: 0,
      playerHand: 0,
      playerDice: [0, 0, 0, 0, 0],
      playerReroll: 0,

      instPage: 1,
      phaseStr: "",
      messageStr: "",

      diceInput0: 0,
      diceInput1: 0,
      diceInput2: 0,
      diceInput3: 0,
      diceInput4: 0,

    }
  },
  methods: {
    rerollTestDice() {
      this.diceInput0 = this.rerollUtils.getRandomDieFace();
      this.diceInput1 = this.rerollUtils.getRandomDieFace();
      this.diceInput2 = this.rerollUtils.getRandomDieFace();
      this.diceInput3 = this.rerollUtils.getRandomDieFace();
      this.diceInput4 = this.rerollUtils.getRandomDieFace();
    }, 
    resetTestDice() {
      this.diceInput0 = 0;
      this.diceInput1 = 0;
      this.diceInput2 = 0;
      this.diceInput3 = 0;
      this.diceInput4 = 0;
    },
    /**
     * section 태그에 마우스 커서가 있으면 특정 함수를 일정 주기마다 호출하도록 만든다.
     */
    isMouseOver() {
      setInterval(this.stateEvent, 500);
    },
    /**
     * 플레이어가 다시 굴릴 주사위를 선택할 때 호출되는 메서드
     * @param {number} idx 플레이어가 선택한 주사위의 인덱스 (0 이상 5 미만의 값)
     */
    clickPlayerDice(idx) {
      if (idx <0 || idx>=this.pokerDiceConsts.NUM_DICE) return; //illegal argument
      switch (this.currentState) {
        case 1: case 3:
          this.playerDice[idx] ^= 1;
          this.playerReroll ^= (1<<idx);
          break;
        default:
          break;
      }
    },
    /**
     * 플레이어가 [다시 굴리기] 버튼을 클릭할 때 호출되는 메서드
     */
    clickRerollButton() {
      switch (this.currentState) {
        case 1:
          for (let i=0; i<this.playerDice.length; i++) {
            if (this.playerDice[i]&1) this.playerDice[i] = 0; //if the player decides to reroll this die... 
          } //i loop
          //this.currentState++;
          this.setStateTo(2);
          break;
        case 3:
          for (let i=0; i<this.playerDice.length; i++) {
            if (this.playerDice[i]&1) this.playerDice[i] = 0; //if the player decides to reroll this die... 
          } //i loop
          //this.currentState++;
          this.setStateTo(4);
          break;
        default:
          break;
      }
    },
    /**
     * 플레이어가 [그만 굴리기] 버튼을 클릭할 때 호출되는 메서드
     */
    clickFixallButton() {
      switch (this.currentState) {
        case 1: case 3:
          //this.currentState = 5;
          this.setStateTo(5);
          break;
        default:
          break;
      }
    },
    clickCheckButton() {
      switch (this.currentState) {
        case 5: //플레이어의 차례 종료 시
          if (this.currentDealer == this.pokerDiceConsts.PLAYER) //this.currentState = 10;
            this.setStateTo(10);
          else //this.currentState = 20;
            this.setStateTo(20);
          break;
        case 15: //컴퓨터의 차례 종료 시
          if (this.currentDealer == this.pokerDiceConsts.PLAYER) //this.currentState = 20;
            this.setStateTo(20);
          else //this.currentState = 0;
            this.setStateTo(0);
          break;
        case 20:
          //this.currentState = 21;
          this.setStateTo(21);
          break;
        default:
          break;
      }
    },
    /**
     * 플레이어가 [처음으로] 버튼을 클릭할 때 호출되는 메서드
     */
    clickResetButton() {
      switch (this.currentState) {
        case 23:
          this.currentRound = 1;
          this.currentDealer = this.pokerDiceConsts.PLAYER;
          this.oppontScore = 0;
          this.oppontHand = 0;
          for (let i=0; i<this.oppontDice.length; i++)
            this.oppontDice[i] = 0;
          this.oppontReroll = 0;
          this.playerScore = 0;
          this.playerHand = 0;
          for (let i=0; i<this.playerDice.length; i++)
            this.playerDice[i] = 0;
          this.playerReroll = 0;
          this.setStateTo(0);
          break;
        default:
          break;
      }
    },
    /**
     * 주기적으로 호출되는 메서드로, 유저의 입력 없이 일어나는 이벤트를 처리한다.<br/>
     * 화면에 출력되는 메시지 변경 역시 수행한다.
     */
    stateEvent() {
      switch (this.currentState) {
        case 0:
          for (let i=0; i<this.playerDice.length; i++)
            this.playerDice[i] = this.rerollUtils.getRandomDieFace();
          this.playerReroll = 0;
          //this.currentState = 1;
          this.setStateTo(1);
          this.playerHand = this.arrayUtils.pokerHand(this.playerDice);
          /* VUE */
          this.messageStr = this.messages.rollPlayerDice;
          this.phaseStr = this.phases.player1st;
          break;
        case 1:
          /* VUE */
          this.messageStr = this.messages.choosePlayerReroll;
          this.phaseStr = this.phases.player2nd;
          break;
        case 2:
          for (let i=0; i<this.playerDice.length; i++) {
            if (this.playerDice[i]==0) this.playerDice[i] = this.rerollUtils.getRandomDieFace();
          } //i loop
          this.playerReroll = 0;
          //this.currentState = 3;
          this.setStateTo(3);
          this.playerHand = this.arrayUtils.pokerHand(this.playerDice);
          /* VUE */
          this.messageStr = this.messages.rerollPlayerDice;
          this.phaseStr = this.phases.player2nd;
          break;
        case 3:
          /* VUE */
          this.messageStr = this.messages.choosePlayerReroll;
          this.phaseStr = this.phases.player3rd;
          break;
        case 4:
          for (let i=0; i<this.playerDice.length; i++) {
            if (this.playerDice[i]==0) this.playerDice[i] = this.rerollUtils.getRandomDieFace();
          } //i loop
          //this.currentState = 5;
          this.setStateTo(5);
          this.playerHand = this.arrayUtils.pokerHand(this.playerDice);
          /* VUE */
          this.messageStr = this.messages.rerollPlayerDice;
          this.phaseStr = this.phases.player3rd;
          break;
        case 5:
          /* VUE */
          this.messageStr = this.messages.endOfPlayerTurn;
          this.phaseStr = this.phases.playerEnd;
          break;
        case 10:
          for (let i=0; i<this.oppontDice.length; i++) 
            this.oppontDice[i] = this.rerollUtils.getRandomDieFace();
          //this.currentState = 11;
          this.setStateTo(11);
          this.oppontHand = this.arrayUtils.pokerHand(this.oppontDice);
          /* VUE */
          this.messageStr = this.messages.rollOppontDice;
          this.phaseStr = this.phases.oppont1st;
          break;
        case 11:
          this.oppontReroll = this.arrayUtils.optReroll(this.oppontDice, this.playerHand);
          if (this.oppontReroll==0) //this.currentState = 15;
            this.setStateTo(15);
          else {
            for (let i=0; i<this.oppontDice.length; i++) {
              if (this.oppontReroll&(1<<i)) this.oppontDice[i] |= 1;
            }
            //this.currentState = 12;
            this.setStateTo(12);
            /* VUE */
            this.messageStr = this.messages.chooseOppontReroll;
            this.phaseStr = this.phases.oppont2nd;
          }
          break;
        case 12:
          for (let i=0; i<this.oppontDice.length; i++) {
            if (this.oppontDice[i]&1) this.oppontDice[i] = this.rerollUtils.getRandomDieFace();
          } //i loop
          //this.currentState = 13;
          this.setStateTo(13);
          this.oppontHand = this.arrayUtils.pokerHand(this.oppontDice);
          /* VUE */
          this.messageStr = this.messages.rerollOppontDice;
          this.phaseStr = this.phases.oppont2nd;
          break;
        case 13:
          this.oppontReroll = this.arrayUtils.optReroll(this.oppontDice, this.playerHand);
          if (this.oppontReroll==0) //this.currentState = 15;
            this.setStateTo(15);
          else {
            for (let i=0; i<this.oppontDice.length; i++) {
              if (this.oppontReroll&(1<<i)) this.oppontDice[i] |= 1;
            } //i loop
            //this.currentState = 14;
            this.setStateTo(14);
          }
          /* VUE */
          this.messageStr = this.messages.chooseOppontReroll;
          this.phaseStr = this.phases.oppont3rd;
          break;
        case 14:
          for (let i=0; i<this.oppontDice.length; i++) {
            if (this.oppontDice[i]&1) this.oppontDice[i] = this.rerollUtils.getRandomDieFace();
          } //i loop
          //this.currentState = 15;
          this.setStateTo(15);
          this.oppontHand = this.arrayUtils.pokerHand(this.oppontDice);
          /* VUE */
          this.messageStr = this.messages.rerollOppontDice;
          this.phaseStr = this.phases.oppont3rd;
          break;
        case 15:
          /* VUE */
          this.messageStr = this.messages.endOfOppontTurn;
          this.phaseStr = this.phases.oppontEnd;
          break;
        case 20:
          /* VUE */
          this.messageStr = this.messages.endOfRound;
          this.phaseStr = this.phases.roundEnd;
          break;
        case 21:
          if (this.oppontHand>this.playerHand) this.oppontScore++;
          else if (this.playerHand>this.oppontHand) this.playerScore++;
          else {
            this.oppontScore += 0.5;
            this.playerScore += 0.5;
          }
          //this.currentState = 22;
          this.setStateTo(22);
          /* VUE */
          this.messageStr = this.messages.default;
          this.phaseStr = this.phases.default;
          break;
        case 22:
          if (this.currentRound>=this.pokerDiceConsts.MAX_ROUND || Math.max(this.oppontScore, this.playerScore)>=(this.pokerDiceConsts.MAX_ROUND+1)/2) {
            //this.currentState = 23;
            this.setStateTo(23);
          } else {
            if (this.oppontHand > this.playerHand) {
              this.currentDealer = this.pokerDiceConsts.OPPONT;
              //this.currentState = 10;
              this.setStateTo(10);
            } else if (this.playerHand > this.oppontHand) {
              this.currentDealer =this.pokerDiceConsts.PLAYER;
              //this.currentState = 0;
              this.setStateTo(0);
            } else if (this.currentDealer == this.pokerDiceConsts.OPPONT) {
              //this.currentState = 10;
              this.setStateTo(10);
            } else {
              //this.currentState = 0;
              this.setStateTo(0);
            }
            this.currentRound++;
            for (let i=0; i<this.oppontDice.length; i++) this.oppontDice[i] = 0;
            for (let i=0; i<this.playerDice.length; i++) this.playerDice[i] = 0;
            this.oppontHand = 0;
            this.playerHand = 0;
          }
          /* VUE */
          this.messageStr = this.messages.default;
          this.phaseStr = this.phases.default;
          break;
        case 23:
          this.messageStr = this.messages.endOfGame;
          this.phaseStr = this.phases.gameEnd;
          break;
      }
    }, 
    /**
     * @param {number} dest 목표 상태
     * 현재 상태를 목표 상태로 변경하되, 500 밀리초의 지연을 준다.
     */
    setStateTo(dest) {
      this.currentState = -1; //상태 전환에 지연을 주기 위한 임의의 상태. 해당 상태는 반드시 [아무 일도 일어나지 않는 무의미한] 상태이어야 한다.
      setTimeout(() => {this.currentState = dest;}, 500);
    }
  }
}
</script>

<style>
.hidden {
  display: none;
}
header{
    background-color: dimgray;
    color: white;
}
.center{
    text-align: center;
}
.right20{
    text-align: right;
    padding-right: 20px;
}
.blackBorder{
  border: 1px solid black;
  margin: 1px;
          width: 8%;
}
.emptyRow10{
    height: 10px;
}
.oppontSide{
    background-color: rgba(255,182,193,0.15);
}
.playerSide{
    background-color: rgba(144,238,144,0.15);
}
.buttonSide{
    background-color: rgba(173,216,230,0.15);
}
.messageSide{
    background-color: rgba(238,130,238,0.15);
    height: 4em;
}
.blue{
    color: deepskyblue;
}
.red{
    color: coral;
}
.green{
    color: turquoise;
}
.modal-body{
    height: 360px;
    overflow-y: scroll;
}
</style>
