import pokerDiceConsts from './pokerDiceConsts.js';
import pokerDiceUtils from './pokerDiceUtils.js';

export default {
    /**
     * 주사위를 다시 굴릴 때 다시 굴릴 주사위를 판별한다.
     * @param {number} die0 
     * @param {number} die1 
     * @param {number} die2 
     * @param {number} die3 
     * @param {number} die4 
     * @param {number} playerHand 
     * @returns {number} 다시 굴릴 주사위를 0 이상 31 이하의 정수로 반환한다. (1: 다시 굴릴 주사위, 0: 고정 시킬 주사위)
     */
    optReroll: (die0, die1, die2, die3, die4, playerHand) => {
        let NUM_FACES = pokerDiceConsts.NUM_FACES;
        let DIE       = [...pokerDiceConsts.DIE]; 
        let oppontOld = pokerDiceUtils.pokerHand(die0, die1, die2, die3, die4);
        let arr = new Array(32);

        for (let i=0; i<arr.length; i++) arr[i] = 0;
        let bestOne = 0;
        for (let j=0; j<32; j++) {
            for (let i0=0; i0<NUM_FACES; i0++) {
                for (let i1=0; i1<NUM_FACES; i1++) {
                    for (let i2=0; i2<NUM_FACES; i2++) {
                        for (let i3=0; i3<NUM_FACES; i3++) {
                            for (let i4=0; i4<NUM_FACES; i4++) {
                                let k0 = (j&1) ? DIE[i0]:die0;
                                let k1 = (j&2) ? DIE[i1]:die1;
                                let k2 = (j&4) ? DIE[i2]:die2;
                                let k3 = (j&8) ? DIE[i3]:die3;
                                let k4 = (j&16)? DIE[i4]:die4;
                                let oppontNew = pokerDiceUtils.pokerHand(k0, k1, k2, k3, k4);
                                arr[j]+=pokerDiceUtils.handGap(oppontNew, playerHand, oppontOld);
                            } //i4 loop
                        } //i3 loop
                    } //i2 loop
                } //i1 loop
            } //i0 loop
            if (arr[j]>arr[bestOne]) bestOne = j;
        } //j loop
        return bestOne;
    },
    /**
     * 임의의 주사위 면 하나를 반환한다.
     * @return {number} 임의의 주사위 면 하나
     */
     getRandomDieFace: () => {
        return pokerDiceConsts.DIE[Math.floor(Math.random()*pokerDiceConsts.NUM_FACES)];
    },
}