import pokerDiceConsts from './pokerDiceConsts.js';
export default {
    /**
     * 주사위 수치를 입력받아 해당하는 이미지파일명을 반환하는 함수
     * @param {number} die 주사위 수치
     * @returns {number} 입력받은 주사위 수치에 해당하는 이미지파일명
     */
    dieToImgFileName: (die) => {
        switch (die) {
            case 10: //dice1.png
            return "data:image/png;base64,";
            case 11: //dice1re.png
            return "data:image/png;base64,";
            case 20: //dice2.png
            return "data:image/png;base64,";
            case 21: //dice2re.png
            return "data:image/png;base64,";
            case 30: //dice3.png
            return "data:image/png;base64,";
            case 31: //dice3re.png
            return "data:image/png;base64,";
            case 40: //dice4.png
            return "data:image/png;base64,";
            case 41: //dice4re.png
            return "data:image/png;base64,";
            case 50: //dice5.png
            return "data:image/png;base64,";
            case 51: //dice5re.png
            return "data:image/png;base64,";
            case 60: //dice6.png
            return "data:image/png;base64,";
            case 61: //dice6re.png
            return "data:image/png;base64,";
            default: //dice0re.png
            return "data:image/png;base64,";
        }
    },
    /**
     * 주사위 면 5개를 입력받아 이에 해당하는 포커 조합을 반환한다.
     * @param {number} die0 주사위 면 (두 자리 정수)
     * @param {number} die1 주사위 면 (두 자리 정수)
     * @param {number} die2 주사위 면 (두 자리 정수)
     * @param {number} die3 주사위 면 (두 자리 정수)
     * @param {number} die4 주사위 면 (두 자리 정수)
     * @return {number} 입력받은 주사위 면 5개에 해당하는 포커 조합 (여섯 자리 정수 또는 0: INVALID)
     */
    pokerHand: (die0, die1, die2, die3, die4) => {
        let dice = [0, 0, 0, 0, 0];
        dice[0] = Math.floor(die0/10);
        dice[1] = Math.floor(die1/10);
        dice[2] = Math.floor(die2/10);
        dice[3] = Math.floor(die3/10);
        dice[4] = Math.floor(die4/10);
        dice.sort((a, b)=>(a-b)); //인자로 받은 die0, die1, ... die4를 정렬한다.
        if (dice[0]<=0) return 0; //invalid
        let liFlag0 = 0, liFlag1 = 0;
        if (dice[0]==dice[1]) liFlag0 |= 8; //인자로 받은 값 중 짝(페어)이 있으면 이를 liFlag0에 기록한다.
        if (dice[1]==dice[2]) liFlag0 |= 4;
        if (dice[2]==dice[3]) liFlag0 |= 2;
        if (dice[3]==dice[4]) liFlag0 |= 1;
        switch (liFlag0) {
            case 15: //aaaaa
                return pokerDiceConsts.FIVE_OF_A_KIND*pokerDiceConsts.TEN_TO_FIVE
                     + dice[0]*pokerDiceConsts.TEN_TO_FOUR;
            case 14: //aaaab
                return pokerDiceConsts.FOUR_OF_A_KIND*pokerDiceConsts.TEN_TO_FIVE
                     + dice[0]*pokerDiceConsts.TEN_TO_FOUR
                     + dice[4]*1000;
            case 7: //abbbb
                return pokerDiceConsts.FOUR_OF_A_KIND*pokerDiceConsts.TEN_TO_FIVE
                     + dice[1]*pokerDiceConsts.TEN_TO_FOUR
                     + dice[0]*1000;
            case 13: //aaabb
                return pokerDiceConsts.FULL_HOUSE*pokerDiceConsts.TEN_TO_FIVE
                     + dice[0]*pokerDiceConsts.TEN_TO_FOUR
                     + dice[3]*1000;
            case 11: //aabbb
                return pokerDiceConsts.FULL_HOUSE*pokerDiceConsts.TEN_TO_FIVE
                     + dice[2]*pokerDiceConsts.TEN_TO_FOUR
                     + dice[0]*1000;
            case 12: //aaabc
                return pokerDiceConsts.THREE_OF_A_KIND*pokerDiceConsts.TEN_TO_FIVE
                     + dice[0]*pokerDiceConsts.TEN_TO_FOUR
                     + dice[4]*1000 + dice[3]*100;
            case 6: //abbbc
                return pokerDiceConsts.THREE_OF_A_KIND*pokerDiceConsts.TEN_TO_FIVE
                     + dice[1]*pokerDiceConsts.TEN_TO_FOUR
                     + dice[4]*1000 + dice[0]*100;
            case 3: //abccc
                return pokerDiceConsts.THREE_OF_A_KIND*pokerDiceConsts.TEN_TO_FIVE
                     + dice[2]*pokerDiceConsts.TEN_TO_FOUR
                     + dice[1]*1000 + dice[0]*100;
            case 10: //aabbc
                return pokerDiceConsts.TWO_PAIR*pokerDiceConsts.TEN_TO_FIVE
                     + dice[2]*pokerDiceConsts.TEN_TO_FOUR
                     + dice[0]*1000 + dice[4]*100;
            case 9: //aabcc
                return pokerDiceConsts.TWO_PAIR*pokerDiceConsts.TEN_TO_FIVE
                     + dice[3]*pokerDiceConsts.TEN_TO_FOUR
                     + dice[0]*1000 + dice[2]*100;
            case 5: //abbcc
                return pokerDiceConsts.TWO_PAIR*pokerDiceConsts.TEN_TO_FIVE
                     + dice[3]*pokerDiceConsts.TEN_TO_FOUR
                     + dice[1]*1000 + dice[0]*100;
            case 8: //aabcd
                liFlag1 = 4; //인자로 받은 값이 원 페어일 경우
                break;
            case 4: //abbcd
                liFlag1 = 3; //인자로 받은 값이 원 페어일 경우
                break;
            case 2: //abccd
                liFlag1 = 2; //인자로 받은 값이 원 페어일 경우
                break;
            case 1: //abcdd
                liFlag1 = 1; //인자로 받은 값이 원 페어일 경우
                break;
            case 0: //abcde
                liFlag1 = 0; //인자로 받은 값이 전부 다른 경우
                break;
        } //switch liFlag0
        switch (liFlag1) {
            case 4: //aabcd
            case 3: //abbcd
            case 2: //abccd
            case 1: //abcdd
                return (dice[4]-dice[0]==pokerDiceConsts.NUM_DICE-2) ?
                       pokerDiceConsts.SMALL_STRAIGHT*pokerDiceConsts.TEN_TO_FIVE
                     + dice[4]*pokerDiceConsts.TEN_TO_FOUR + dice[pokerDiceConsts.NUM_DICE-1-liFlag1]*1000 :
                       pokerDiceConsts.BUST*pokerDiceConsts.TEN_TO_FIVE
                     + dice[4]*pokerDiceConsts.TEN_TO_FOUR + dice[3]*1000 + dice[2]*100 + dice[1] * 10 + dice[0] * 1;
            case 0: //abcde
                return (dice[4]-dice[0]==pokerDiceConsts.NUM_DICE-1) ? //abcde
                            pokerDiceConsts.LARGE_STRAIGHT*pokerDiceConsts.TEN_TO_FIVE
                          + dice[4]*pokerDiceConsts.TEN_TO_FOUR :
                        (dice[3]-dice[0]==pokerDiceConsts.NUM_DICE-2) ? //abcdf
                            pokerDiceConsts.SMALL_STRAIGHT*pokerDiceConsts.TEN_TO_FIVE
                          + dice[3]*pokerDiceConsts.TEN_TO_FOUR + dice[4]*1000 :
                        (dice[4]-dice[1]==pokerDiceConsts.NUM_DICE-2) ? //acdef
                            pokerDiceConsts.SMALL_STRAIGHT*pokerDiceConsts.TEN_TO_FIVE
                          + dice[4]*pokerDiceConsts.TEN_TO_FOUR + dice[0]*1000 :
                            pokerDiceConsts.BUST*pokerDiceConsts.TEN_TO_FIVE
                          + dice[4]*pokerDiceConsts.TEN_TO_FOUR + dice[3]*1000 + dice[2]*100 + dice[1] * 10 + dice[0] * 1;
        }
        return 0;
    },
    /**
     * 입력받은 포커 조합을 풀이하여 문자열로 반환한다.
     * @param {number} pokerHand 반환할 포커 조합, 6자리 정수
     * @return {string} 입력받은 포커 조합을 문자열로 반환한 결과
     */
    handToString: (pokerHand) => {
        let pokerHandStr = String(pokerHand);
        if (pokerHandStr.length!=6)
            return "";
        let handType = parseInt(pokerHandStr[0]);
        let rank1    = pokerHandStr[1];
        let rank2    = pokerHandStr[2];
        let rank3    = pokerHandStr[3];
        let rank4    = pokerHandStr[4];
        let rank5    = pokerHandStr[5];
        switch (handType) {
            case pokerDiceConsts.FIVE_OF_A_KIND:
                return `퀸트, ${rank1}`;
            case pokerDiceConsts.FOUR_OF_A_KIND:
                return `쿼드, ${rank1}, ${rank2}`;
            case pokerDiceConsts.LARGE_STRAIGHT:
                return `라지 스트레이트, ${rank1}`;
            case pokerDiceConsts.FULL_HOUSE:
                return `풀 하우스, ${rank1}, ${rank2}`;
            case pokerDiceConsts.SMALL_STRAIGHT:
                return `스몰 스트레이트, ${rank1}, ${rank2}`;
            case pokerDiceConsts.THREE_OF_A_KIND:
                return `트립, ${rank1}, ${rank2}, ${rank3}`;
            case pokerDiceConsts.TWO_PAIR:
                return `투 페어, ${rank1}, ${rank2}, ${rank3}`;
            case pokerDiceConsts.BUST:
                return `버스트, ${rank1}, ${rank2}, ${rank3}, ${rank4}, ${rank5}`;
            default:
                return "";
        } //switch handType
    },
    handGap: (oppontNew, playerHand, oppontOld) => {
        let TEN_TO_FOUR = 10000;
        let TEN_TO_FIVE = 100000;
        if (oppontOld>=playerHand) {
            if (oppontNew>=playerHand)
                return (oppontNew-playerHand)/TEN_TO_FOUR;
            else
                return 100*(oppontNew-playerHand)/TEN_TO_FOUR;
        } else {
            if (oppontNew>=playerHand)
                return 2*(oppontNew-playerHand)/TEN_TO_FOUR;
            else
                return (oppontNew-playerHand)/TEN_TO_FIVE;
        }
    }
};